@import url('https://fonts.googleapis.com/css2?family=Gowun+Batang:wght@400;700&display=swap');


.product{
  cursor: pointer;
}
.title {
  color : black;
  text-decoration: none;
  font-weight: normal;
  font-style: italic;
}
.btn-danger {
  margin: 0 5px;
}

.jumbotron {
  width: 100%;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  background-color: rgb(238, 238, 238);
}
.jumbotron h1,
.jumbotron p {
  font-size: 26px;
  font-family: 'Gowun Batang', serif;
  font-weight: bold;
  margin-bottom: 5px;
}
.jumbotron .tel {
  font-size: 20px;
}
.jumbotron img {
  padding: 15px;
  height: 250px;
  border-radius: 30%;
}

.jumbo_wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.jumbo_wrap i {
  width: 100px;
}
.jumbo_wrap .imgTarget {
  width: 70px;
  height: 70px;
}
.jumbo_wrap .tel {
  font-size: 18px;
}
.jumbo_wrap .secondDiv p:first-child {
  margin-top: 30px;
}



.product .product_wrap img {
  margin-bottom: 20px;
}

.product .product_wrap h4.title {
  font-weight: bold;
}
.product .product_wrap h4.price {
  font-style: italic;
}
.product .product_wrap p {
  font-style: italic;
}
.link1 {
  text-decoration: none;
  font-style: normal;
  color: black;
}
.link1 p {
  
}
.link1 p:hover {
  transform: scale(1.1);
  font-weight: bold;
  color: black;
  transition: 0.2s;
}
.link2:hover {
  transform: scale(1.1);
  font-weight: bold;
  color: black;
  transition: 0.2s;
}

.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
